import React, { CSSProperties, ReactElement } from 'react';
import path from 'path';

import styles from '../assets/styles/projects.module.scss';

import { backend } from '../assets/config.json';

export default class ImageBox extends React.Component<{
	image: string
	style: CSSProperties
}> {

	get defaultStyle() {
		return {
			backgroundColor: 'black',
			backgroundImage: `url(${path.join(backend, 'artwork', this.props.image)})`,
			backgroundSize: 'cover',
			backgroundPosition: 'center',
			backgroundRepeat: 'no-repeat'
		} as CSSProperties;
	}

	render(): ReactElement {
		let style = Object.assign(this.defaultStyle, this.props.style);
		return (
			<div
				className={[styles.imageBox, styles['project-text']].join(' ')}
				style={style}
			/>
		);
	}
}